<template>
    <div class="my-blog">
       <article @click="toDetails(url)">
            <h4 class="title">
                {{title}}
            </h4>
            <p class="time">
                {{time}}
                <el-tag v-for="item in tags"
                        :type="item.type"
                        :key="item.type">{{item.name}}</el-tag>
                {{distance(time)}}天前
            </p>
            <p v-html="returnBrief(brief)"
               class="brief">
                {{brief}}
            </p>
        </article>
    </div>
</template>
<script>
    export default {
        props:{
            title : {type:String,default:''},
            url   : {type:String,default:''},
            time  : {type:String,default:''},
            tags  : {type:Array ,default:[]},
            brief : {type:String,default:''},
        },
        methods:{
            // 跳转至详情
            toDetails(path){
                this.$router.push( '/blog/blog_' + path );
            },
            // 过滤标签后固定显示最多75字简介
            returnBrief(val){
                const _val = val.replace(/<[^>]+>/g,"");
                return _val.substring( 0,75 ) + ( _val.length <= 75 ? '' : '...' );
            },
            // 计算距今的时间（天数）
            distance(time){
                let now = new Date();
                let [ year, month, date ] = [ time.substring(0,4), time.substring(5,7), time.substring(8,10) ];
                let before = new Date( year, month - 1, date );
                return ( ( now - before ) / 1000 / 60 / 60 / 24 ) >> 0;
            },
        }
    }
</script>
<style>
    .my-blog article{
        cursor: pointer;
        padding:6px;
        height:150px;
        overflow:hidden;
       /* -webkit-box-shadow: 0 0 10px 1px #ddd;
        -moz-box-shadow: 0 0 10px 1px #ddd;
        box-shadow: 0 0 10px 1px rgba(240,255,240,1);*/
        font-size:14px;
        transition: all 1s ease-in-out;
        -webkit-transition: all 0.4s ease-in-out;
        -moz-transition: all 0.4s ease-in-out;
        -o-transition: all 0.4s ease-in-out;
    }
    .my-blog article p{
        margin:5px 0;
        font-size:1em;
    }
    .my-blog article .title{
        border-left:8px solid #23aa23;
        margin:10px 0;
        padding-left:4px;
        color:#475669;
        font-size:1.2em;
    }
    .my-blog article .time{
        padding-left:10px;
        color:#99A9BF;
        font-size:0.9em;
    }
    .my-blog article .brief{
        padding:0 10px;
        color:#8492A6;
        font-size:1em;
    }
    .my-blog .el-tag{
        margin:0 2px;
    }
    @media screen and ( min-width: 750px ) {
        .my-blog article:hover{
            font-size:16px;
        }
    }
</style>


